import { useState } from "react";
import PokeCard from "./PokeCard";

function App() {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [input, setInput] = useState("");

  const fetchData = (api) => {
    fetch(api)
      .then((response) => {
        if (!response.ok) {
          throw new Error(`No such pokemon: ${response.status}`);
        }
        return response.json();
      })

      .then((response) => {
        setData(response);
        console.log(data);
        setError(null);
      })

      .catch((err) => {
        setError(err.message);
        setData(null);
      })

      .finally(() => {
        setLoading(false);
      });
  };

  const handleChange = (e) => {
    setInput(e.target.value);
  };

  const handleEnter = (e) => {
    if (e.key === "Enter") {
      setInput(e.target.value);
      const api = `https://pokeapi.co/api/v2/pokemon/${input}`;
      console.log(api);
      fetchData(api);
    }
  };

  return (
    <div className="app">
      <h1>The Pokedex Project</h1>
      <input
        className="search-bar"
        type="text"
        placeholder="Search pokemon..."
        onChange={handleChange}
        onKeyDown={handleEnter}
      />

      {loading && <div>Searching for pokemons...</div>}
      {error && <div>{error}</div>}

      {data !== null && !loading && (
        <PokeCard
          key={data.id}
          name={data.name}
          height={data.height}
          weight={data.weight}
          front={data.sprites.front_default}
          back={data.sprites.back_default}
        />
      )}
    </div>
  );
}

export default App;
